<template>
  <div>
    <router-view />
  </div>
  <!-- toast -->
  <div class="row">
    <div class="toast-container position-absolute top-0 end-0 p-5" style="z-index: 9999">
      <div id="toast" class="toast" role="alert" aria-live="assertive" aria-atomic="true">
        <div :class="'toast-header text-light ' + $store.getters.toast_bgcolor">
          <img class="rounded me-2" />
          <strong class="me-auto"><i class="bi bi-exclamation-circle"></i>&nbsp;{{
            $store.getters.toast_head
          }}</strong>
          <button type="button" class="btn-close" data-bs-dismiss="toast" aria-label="Close"></button>
        </div>
        <div class="toast-body set_l">{{ $store.getters.toast_body }}</div>
      </div>
    </div>
  </div>
  <!-- toast -->
</template>
<script setup>
import { ref, provide, inject, onMounted, nextTick, reactive } from 'vue'
import { useRouter } from 'vue-router'
import { useStore } from 'vuex'

const store = useStore()
const router = useRouter()

const sysName = ref('Safety Activity Apps')
// const profile = ref('')

const bootstrap = inject('bootstrap')
const moment = inject('moment')
const introJs = inject('introJs')
// const axios = inject('axios')

const eqColorHQ = reactive([
  { value: 'HQ1', text: 'success' },
  { value: 'HQ2', text: 'danger' },
  { value: 'HQ3', text: 'primary' },
  { value: 'HQ4', text: 'secondary' },
  { value: 'HQ5', text: 'dark' },
  { value: 'HQ6', text: 'info' },
  { value: 'HQ7', text: 'secondary' },
  { value: 'HQ8', text: 'dark' },
  { value: 'HQ9', text: 'primary' },
  { value: 'HQ10', text: 'warning' }
])
const eqIconHQ = reactive([
  { value: 'HQ1', text: 'bug-fill' },
  { value: 'HQ2', text: 'people-fill' },
  { value: 'HQ3', text: 'person-bounding-box' },
  { value: 'HQ4', text: 'arrow-left-right' },
  { value: 'HQ5', text: 'award' },
  { value: 'HQ6', text: 'bar-chart-fill' },
  { value: 'HQ7', text: 'bell-fill' },
  { value: 'HQ8', text: 'battery-full' },
  { value: 'HQ9', text: 'box-seam' },
  { value: 'HQ10', text: 'shield-check' }
])

onMounted(() => {
  saveLogin()
  enableTooltips()
  getImage()
})

const getImage = () => { }
const saveLogin = () => { }
const checkTypeMail = () => {
  nextTick(() => {
    // const alert = bootstrap.Alert.getOrCreateInstance('#myTypeMail')
    // if (store.getters.getTypeCompany === 'ais') {
    //   alert.close()
    // } else {
    //   console.log('NO AIS')
    // }
  })
}
const alertToasts = (head, body, color) => {
  store.commit('change_head_toast', head)
  store.commit('change_body_toast', body)
  store.commit('change_bgcolor_toast', color)
  const toastLiveExample = document.getElementById('toast')
  const toastBootstrap = bootstrap.Toast.getOrCreateInstance(toastLiveExample)
  toastBootstrap.show()
}
const momentFormat = (val1, val2) => {
  const text = moment(val1).format(val2)
  return text
}
const enableTooltips = () => {
  setTimeout(() => {
    const tooltipTriggerList = document.querySelectorAll(
      '[data-bs-toggle="tooltip"]'
    )
    tooltipTriggerList.forEach((element) => new bootstrap.Tooltip(element))
  }, 1000)
}
const goToRouteName = (value) => {
  router.push({ name: value })
}
const goToRoutePath = (value1, value2) => {
  router.push({ path: '/' + value1 + '/' + value2 })
}
const copyURL = (value) => {
  navigator.clipboard.writeText(value)
  alertToasts('Copy', value, 'bg-info')
}
const goLinkUse = (value) => {
  window.location.href = value
}
const goLinkUseNewTab = (value) => {
  window.open(value, '_blank')
}
const introSector = (value) => {
  introJs('#' + value)
    .setOptions({
      showProgress: false,
      nextLabel: 'ต่อไป',
      prevLabel: 'กลับ',
      doneLabel: 'สำเร็จ!'
    })
    .start()
}
provide('momentFormat', momentFormat)
provide('alertToasts', alertToasts)
provide('enableTooltips', enableTooltips)
provide('goToRouteName', goToRouteName)
provide('goToRoutePath', goToRoutePath)
provide('goLinkUse', goLinkUse)
provide('goLinkUseNewTab', goLinkUseNewTab)
provide('copyURL', copyURL)
provide('sysName', sysName)
provide('checkTypeMail', checkTypeMail)
provide('introSector', introSector)
provide('eqColorHQ', eqColorHQ)
provide('eqIconHQ', eqIconHQ)

</script>

