import { createApp } from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import './assets/tailwind.css'
import router from './router'
import store from './store'
import * as bootstrap from 'bootstrap'
import VueQRCodeComponent from 'vue-qrcode-component'
import SignaturePad from 'signature_pad'
import moment from 'moment'
import axios from 'axios'
import jquery from 'jquery'
import masonry from 'masonry-layout'
import introJs from 'intro.js'

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-icons/font/bootstrap-icons.min.css'
import 'bootstrap-fileinput/css/fileinput.min.css'
import 'bootstrap-fileinput/themes/explorer/theme.min.css'
import 'intro.js/introjs.css'
import 'intro.js/themes/introjs-modern.css'

import 'bootstrap-fileinput/js/fileinput.min.js'
import 'bootstrap-fileinput/themes/bs5/theme.min.js'
import 'bootstrap-fileinput/themes/explorer/theme.min.js'

// window.$ = window.jQuery = require('jquery')
// window.$axios = window.axios = require('axios')
// window.$moment = window.moment = require('moment')
// window.$bootstrap = window.bootstrap = bootstrap
// window.SignaturePad = SignaturePad

const app = createApp(App)
app.use(store)
app.use(router)
app.component('qr-code', VueQRCodeComponent)
app.provide('moment', moment)
app.provide('bootstrap', bootstrap)
app.provide('axios', axios)
app.provide('$', jquery)
app.provide('SignaturePad', SignaturePad)
app.provide('Masonry', masonry)
app.provide('introJs', introJs)
app.mount('#app')
