import { createRouter, createWebHashHistory } from 'vue-router'
// main
import StartPage from '../views/main/StartPage.vue'
import BlankPage from '../views/main/SetBlankPage.vue'
// activity
import MainActivity from '../views/activity/MainActivity.vue'
import acMenuMain from '../views/activity/MainMenu.vue'
// audit
import MainAudit from '../views/audit/MainAudit.vue'
// equipment
import MainEquipment from '../views/equipment/MainEquipment.vue'
import eqMainStart from '../views/equipment/MainStart.vue'
import eqHqMenu from '../views/equipment/hq/HqMenu.vue'
import eqHqPage from '../views/equipment/hq/HqPage.vue'
import eqStoreMenu from '../views/equipment/store/StoreMenu.vue'
import eqStorePage from '../views/equipment/store/StorePage.vue'
import eqMyMenu from '../views/equipment/my/MyMenu.vue'
import eqMyPage from '../views/equipment/my/MyPage.vue'
import eqReportMenu from '../views/equipment/report/ReportMenu.vue'
import eqReportPage from '../views/equipment/report/ReportPage.vue'
import eqContactMenu from '../views/equipment/contact/ContactMenu.vue'
import eqContactPage from '../views/equipment/contact/ContactPage.vue'
import eqAdminMenu from '../views/equipment/admin/AdminMenu.vue'
import eqAdminPage from '../views/equipment/admin/AdminPage.vue'
// form
import MainForm from '../views/form/MainForm.vue'

const routes = [
  {
    path: '/',
    name: 'StartPage',
    component: StartPage,
    meta: {
      requiresAuth: true,
      title: 'SAA Home'
    }
  },
  {
    path: '/activity/',
    name: 'MainActivity',
    component: MainActivity,
    meta: {
      requiresAuth: true,
      title: 'Activity Home'
    },
    children: [
      {
        path: 'menu',
        name: 'acMenuMain',
        meta: {
          requiresAuth: true,
          title: 'Activity Menu'
        },
        components: {
          safety_activity_body: acMenuMain
        }
      }
    ]
  },
  {
    path: '/audit',
    name: 'MainAudit',
    component: MainAudit,
    meta: {
      requiresAuth: true,
      title: 'Audit Home'
    }
  },
  {
    path: '/equipment/',
    name: 'MainEquipment',
    component: MainEquipment,
    meta: {
      requiresAuth: true,
      title: 'SE Home'
    },
    children: [
      {
        path: 'start',
        name: 'eqMainStart',
        meta: {
          requiresAuth: true,
          title: 'SE Start'
        },
        components: {
          safety_equipment_body: eqMainStart
        }
      },
      {
        path: 'qrmenu',
        name: 'eqHqMenu',
        meta: {
          requiresAuth: true,
          title: 'SE HQ Menu'
        },
        components: {
          safety_equipment_body: BlankPage,
          sub_menu: eqHqMenu,
          sub_body: eqHqPage
        }
      },
      {
        path: 'storemenu',
        name: 'eqStoreMenu',
        meta: {
          requiresAuth: true,
          title: 'SE Store Menu'
        },
        components: {
          safety_equipment_body: BlankPage,
          sub_menu: eqStoreMenu,
          sub_body: eqStorePage
        }
      },
      {
        path: 'mymenu',
        name: 'eqMyMenu',
        meta: {
          requiresAuth: true,
          title: 'SE My Menu'
        },
        components: {
          safety_equipment_body: BlankPage,
          sub_menu: eqMyMenu,
          sub_body: eqMyPage
        }
      },
      {
        path: 'reportmenu',
        name: 'eqReportMenu',
        meta: {
          requiresAuth: true,
          title: 'SE Report Menu'
        },
        components: {
          safety_equipment_body: BlankPage,
          sub_menu: eqReportMenu,
          sub_body: eqReportPage
        }
      },
      {
        path: 'contactmenu',
        name: 'eqContactMenu',
        meta: {
          requiresAuth: true,
          title: 'SE Contact Menu'
        },
        components: {
          safety_equipment_body: BlankPage,
          sub_menu: eqContactMenu,
          sub_body: eqContactPage
        }
      },
      {
        path: 'adminmenu',
        name: 'eqAdminMenu',
        meta: {
          requiresAuth: true,
          title: 'SE Admin Menu'
        },
        components: {
          safety_equipment_body: BlankPage,
          sub_menu: eqAdminMenu,
          sub_body: eqAdminPage
        }
      }
    ]
  },
  {
    path: '/form',
    name: 'MainForm',
    component: MainForm,
    meta: {
      requiresAuth: true,
      title: 'Form Home'
    }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})
const DEFAULT_TITLE = 'Safety Activity Apps'
router.beforeEach((to, from, next) => {
  document.title = to.meta.title || DEFAULT_TITLE
  next()
})

export default router
